import React, { useState } from 'react';
import { ListItem, ListItemText, IconButton, TextField, Button } from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import SaveIcon from '@mui/icons-material/Save';
import CancelIcon from '@mui/icons-material/Cancel';

const EventItem = ({ event, onDelete, onUpdate }) => {
    const [isEditing, setIsEditing] = useState(false);
    const [updatedEventName, setUpdatedEventName] = useState(event.name);
    const [updatedEventDate, setUpdatedEventDate] = useState(event.date);

    const handleEdit = () => {
        setIsEditing(true);
    };

    const handleCancel = () => {
        setIsEditing(false);
        setUpdatedEventName(event.name);
        setUpdatedEventDate(event.date);
    };

    const handleSave = () => {
        onUpdate(event.id, updatedEventName, updatedEventDate);
        setIsEditing(false);
    };

    return (
        <ListItem sx={{ display: 'flex', alignItems: 'center' }}>
            {isEditing ? (
                <>
                    <TextField
                        value={updatedEventName}
                        onChange={(e) => setUpdatedEventName(e.target.value)}
                        sx={{ marginRight: 2 }}
                    />
                    <TextField
                        value={updatedEventDate}
                        onChange={(e) => setUpdatedEventDate(e.target.value)}
                        sx={{ marginRight: 2 }}
                    />
                    <Button onClick={handleSave} variant="contained" color="primary" sx={{ marginRight: 2 }}>
                        <SaveIcon />
                    </Button>
                    <Button onClick={handleCancel} variant="contained" color="secondary">
                        <CancelIcon />
                    </Button>
                </>
            ) : (
                <>
                    <ListItemText primary={event.name} secondary={event.date} />
                    <IconButton onClick={handleEdit} sx={{ marginRight: 2 }}>
                        <EditIcon />
                    </IconButton>
                    <IconButton onClick={() => onDelete(event.id)} color="secondary">
                        <DeleteIcon />
                    </IconButton>
                </>
            )}
        </ListItem>
    );
};

export default EventItem;
