import { HDate } from '@hebcal/core';

export const exodusDate = new HDate(1, 1, 2448); // 1 Nisan 2448

export const formatDuration = (days, months, years) => {
    return `${days}.${months}.${years}`;
};

export const convertToExodusDiff = (date) => {
    const hd = new HDate(new Date(date));
    const years = hd.getFullYear() - exodusDate.getFullYear();
    const months = hd.getMonth() - exodusDate.getMonth();
    const days = hd.getDate() - exodusDate.getDate();
    return formatDuration(days, months, years);
};
