import React, { useState } from 'react';
import {
    Dialog, DialogActions, DialogContent, DialogTitle,
    TextField, Button, FormControl, RadioGroup, FormControlLabel, Radio
} from '@mui/material';

const AddEventDialog = ({ open, onClose, onAddEvent }) => {
    const [newEventName, setNewEventName] = useState('');
    const [newEventDate, setNewEventDate] = useState('');
    const [selectedOption, setSelectedOption] = useState('regular');

    const handleOptionChange = (event) => {
        setSelectedOption(event.target.value);
    };

    const handleAddEvent = () => {
        const isRegularDate = selectedOption === 'regular';
        onAddEvent(newEventName, newEventDate, isRegularDate);
        setNewEventName('');
        setNewEventDate('');
        onClose();
    };

    return (
        <Dialog open={open} onClose={onClose}>
            <DialogTitle>Add a New Event</DialogTitle>
            <DialogContent>
                <TextField
                    autoFocus
                    margin="dense"
                    label="Event Name"
                    fullWidth
                    variant="outlined"
                    value={newEventName}
                    onChange={(e) => setNewEventName(e.target.value)}
                />

                <FormControl component="fieldset" sx={{ marginTop: 2 }}>
                    <RadioGroup
                        aria-label="event-date-options"
                        name="event-date-options"
                        value={selectedOption}
                        onChange={handleOptionChange}
                        row
                    >
                        <FormControlLabel value="regular" control={<Radio />} label="Regular Date" />
                        <FormControlLabel value="fromExodus" control={<Radio />} label="Date from Exodus" />
                    </RadioGroup>
                </FormControl>

                <TextField
                    margin="dense"
                    label="Event Date"
                    type={selectedOption === 'regular' ? 'date' : 'text'}
                    placeholder={selectedOption === 'fromExodus' ? 'DD.MM.YYYY' : ''}
                    fullWidth
                    variant="outlined"
                    InputLabelProps={{
                        shrink: true,
                    }}
                    value={newEventDate}
                    onChange={(e) => setNewEventDate(e.target.value)}
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose} color="secondary">
                    Cancel
                </Button>
                <Button onClick={handleAddEvent} color="primary">
                    Add
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default AddEventDialog;
