import React from 'react';
import { Box, List, Divider } from '@mui/material';
import EventItem from './EventItem';

const EventTimeline = ({ events, onDelete, onUpdate }) => {
    const sortedEvents = events.sort((a, b) => {
        const [aDays, aMonths, aYears] = a.date.split('.').map(Number);
        const [bDays, bMonths, bYears] = b.date.split('.').map(Number);

        const aDate = new Date(aYears, aMonths - 1, aDays);
        const bDate = new Date(bYears, bMonths - 1, bDays);

        return aDate - bDate;
    });

    return (
        <Box sx={{ position: 'relative', paddingLeft: 4 }}>
            <Box
                sx={{
                    position: 'absolute',
                    top: 0,
                    bottom: 0,
                    left: 0,
                    width: 2,
                    bgcolor: 'grey.400',
                    marginLeft: '18px',
                }}
            />
            <List>
                {sortedEvents.map((event, index) => (
                    <React.Fragment key={event.id}>
                        <div className="timeline-dot"></div>
                        <EventItem event={event} onDelete={onDelete} onUpdate={onUpdate} />
                        {index < sortedEvents.length - 1 && <Divider />}
                    </React.Fragment>
                ))}
            </List>
        </Box>
    );
};

export default EventTimeline;
